<template>
  <div id="app">
    <div id="cover"></div>
      <Header></Header>
      <Todo></Todo>
      <Footer></Footer>
  </div>
</template>

<script>
import Header from "./todo/header.vue";
import Todo from "./todo/todo.vue";
import Footer from "./todo/footer.jsx";
export default {
  components: {
    Header,
    Footer,
    Todo
  }
};
</script>

<style lang="stylus" scoped>
#app {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

#cover {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(153, 153, 153, 0.3);
  z-index: -1;
}
</style>



